.body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

    
.gallery {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.gallery > * {
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-flex-basis: 300px;
            flex-basis: 300px;
    position: relative;
    overflow: hidden;
}

.cover img {
    height: 100%;
    width: 100%;
}

.cover-overlay {
    transition: bottom 0.5s ease;
    bottom: -100%;
    position: absolute;
    opacity: .8;
    background-color:white;
    color: black;
    text-align: center;
    padding: 30px;
    width: 100%;
    box-sizing: border-box;
}

.cover:hover .cover-overlay {
    bottom: 0;
}

.details-content {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 0 30px;
}

.details-content-synopsis {
    padding-right: 30px;
}

.details-content-cover img {
    max-width: 300px;
}

@media screen and (max-width: 640px) {

    .details-content {
        display: block;
    }

    .details-content-cover img {
        max-width: 100%;
    }
}
.App {
  text-align: center;
}

img {
  width: 100%
}

.container {
  /*display: flex;
  flex-wrap: wrap;*/
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
}

.item {
  /*flex: 1;
  flex-basis: 120px;*/
  padding: 30px;
  position: relative;
  border: 1px solid white;
  overflow: hidden;
}

.overlay {
  transition: bottom ease 1s;
  position: absolute;
  bottom: -100px;
  left: 0;
  width: 100%;
  opacity: 0.6;
  
}

.item:hover .overlay {
  opacity: .5;
  bottom: 0;
}



